/* ==========================================================================
   WooCommerce Cart page responsive styles - begin
   ========================================================================== */

@include ipad-portrait {
	
	.mkdf-woocommerce-page {
		
		table.cart {
			
			td.actions {
				
				.coupon {
					
					input[type="text"] {
						width: 180px;
						margin: -1px 17px 0 0;
						padding: 12px 20px;
					}
					
					.button{
						margin: -1px 0 0 0;
					}
					
				}
			}
		}
	}
}

@include phone-landscape {
	
	.mkdf-woocommerce-page {
		
		table.cart {
			
			tr {
				
				th, td {
					font-size: 12px;
				}
				
				&.cart_item {
					
					td {
						
						&.product-thumbnail {
							width: auto;
							padding-right: 0;
							
							img {
								display: none;
							}
						}
					}
				}
			}
			
			td.actions {
				
				.coupon {
					margin: 0 0 20px;
					
					input[type="text"] {
						width: 100%;
						padding: 12px 15px;
						margin: 0 10px 20px 0;
					}
				}
				
				.button{
					float: none;
					width: 100%;
				}
				
				.coupon,
				> input,
				> button {
					float: none;
					display: block;
				}
			}
		}

		.cart-collaterals {
			.wc-proceed-to-checkout {
				a {
					width: 100%;
					text-align: center;
				}
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Cart page responsive styles - end
   ========================================================================== */