/* ==========================================================================
   WooCommerce Message Responsive styles - begin
   ========================================================================== */

@media only screen and (max-width: $ipad-landscape) {

    .mkdf-woocommerce-page {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 17px;
        }
        
        .woocommerce-message{
            a{
                line-height: 24px!important;
                padding: 12px 0!important;
            }
        }
    }
}

@media only screen and (max-width: $phone-landscape) {

    .mkdf-woocommerce-page {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 14px 20px;
            line-height: 2em;

            a {
            	float: none;
                margin: 0 70% 15px 0;
            }
        }
    
        .woocommerce-message{
            a {
                text-align: center;
                display: block !important;
                margin: 0 auto 20px!important;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Message Responsive styles - end
   ========================================================================== */