/* ==========================================================================
   Product List shortcode responsive styles - begin
   ========================================================================== */
@include laptop-landscape-large {

	.mkdf-pl-holder {

		&.mkdf-standard-layout {
			$standard_list_columns: ('six');

			@for $i from 0 to length($standard_list_columns) {
				&.mkdf-#{nth($standard_list_columns,$i+1)}-columns {
					$column_width: 100%/5;

					.mkdf-pli {
						width: $column_width;

						@media only screen and (min-width: $laptop-landscape-mac-plus-pixel) {
							&:nth-child(6n+1) {
								clear: none;
							}

							&:nth-child(5n+1) {
								clear: both;
							}
						}
					}
				}
			}
		}

		&.mkdf-masonry-layout {
			$masonry_list_columns: ('six');

			@for $i from 0 to length($masonry_list_columns) {
				&.mkdf-#{nth($masonry_list_columns,$i+1)}-columns {
					$column_width: 100%/5;

					.mkdf-pli,
					.mkdf-pl-sizer {
						width: $column_width;
					}

					.mkdf-pli.mkdf-woo-image-large-width,
					.mkdf-pli.mkdf-woo-image-large-width-height {
						width: $column_width*2;
					}
				}
			}
		}
	}
}

@include laptop-landscape-mac {

	.mkdf-pl-holder {

		&.mkdf-standard-layout {
			$standard_list_columns: ('five', 'six');

			@for $i from 0 to length($standard_list_columns) {
				&.mkdf-#{nth($standard_list_columns,$i+1)}-columns {
					$column_width: 100%/4;

					.mkdf-pli {
						width: $column_width;

						@media only screen and (min-width: $ipad-landscape-plus-pixel) {

							&:nth-child(4n+1) {
								clear: both;
							}
						}
					}
				}
			}
		}

		&.mkdf-masonry-layout {
			$masonry_list_columns: ('five', 'six');

			@for $i from 0 to length($masonry_list_columns) {
				&.mkdf-#{nth($masonry_list_columns,$i+1)}-columns {
					$column_width: 100%/4;

					.mkdf-pli,
					.mkdf-pl-sizer {
						width: $column_width;
					}

					.mkdf-pli.mkdf-woo-image-large-width,
					.mkdf-pli.mkdf-woo-image-large-width-height {
						width: $column_width*2;
					}
				}
			}
		}
	}
}

@include ipad-landscape {

	.mkdf-pl-holder {

		&.mkdf-standard-layout {
			$standard_list_columns: ('four', 'five', 'six');

			@for $i from 0 to length($standard_list_columns) {
				&.mkdf-#{nth($standard_list_columns,$i+1)}-columns {
					$column_width: 100%/3;

					.mkdf-pli {
						width: $column_width;

						@media only screen and (min-width: $ipad-portrait-plus-pixel) {
							&:nth-child(3n+1) {
								clear: both;
							}
						}
					}
				}
			}

			.mkdf-pli {
				.mkdf-pli-price {
					position: relative;
					top: 0;
				}
			}
		}

		&.mkdf-masonry-layout {
			$masonry_list_columns: ('four', 'five', 'six');

			@for $i from 0 to length($masonry_list_columns) {
				&.mkdf-#{nth($masonry_list_columns,$i+1)}-columns {
					$column_width: 100%/3;

					.mkdf-pli,
					.mkdf-pl-sizer {
						width: $column_width;
					}

					.mkdf-pli.mkdf-woo-image-large-width,
					.mkdf-pli.mkdf-woo-image-large-width-height {
						width: $column_width*2;
					}
				}
			}
		}
	}
}

@include ipad-portrait {

	.mkdf-pl-holder {

		&.mkdf-standard-layout {
			$standard_list_columns: ('two', 'three', 'four', 'five', 'six');

			@for $i from 0 to length($standard_list_columns) {
				&.mkdf-#{nth($standard_list_columns,$i+1)}-columns {
					$column_width: 50%;

					.mkdf-pli {
						width: $column_width;

						@media only screen and (min-width: $phone-landscape-plus-pixel) {
							&:nth-child(2n+1) {
								clear: both;
							}
						}
					}
				}
			}
		}

		&.mkdf-masonry-layout {
			$masonry_list_columns: ('three', 'four', 'five', 'six');

			@for $i from 0 to length($masonry_list_columns) {
				&.mkdf-#{nth($masonry_list_columns,$i+1)}-columns {
					$column_width: 50%;

					.mkdf-pli,
					.mkdf-pl-sizer {
						width: $column_width;
					}

					.mkdf-pli.mkdf-woo-image-large-width,
					.mkdf-pli.mkdf-woo-image-large-width-height {
						width: $column_width*2;
					}
				}
			}
		}
	}
}

@include phone-landscape {

	.mkdf-pl-holder {

		&.mkdf-standard-layout {
			$standard_list_columns: ('two', 'three', 'four', 'five', 'six');

			@for $i from 0 to length($standard_list_columns) {
				&.mkdf-#{nth($standard_list_columns,$i+1)}-columns {

					.mkdf-pli {
						width: 100%;
					}
				}
			}
		}

		&.mkdf-masonry-layout {
			$masonry_list_columns: ('two', 'three', 'four', 'five', 'six');

			@for $i from 0 to length($masonry_list_columns) {
				&.mkdf-#{nth($masonry_list_columns,$i+1)}-columns {

					.mkdf-pli,
					.mkdf-pl-sizer,
					.mkdf-pli.mkdf-woo-image-large-width,
					.mkdf-pli.mkdf-woo-image-large-width-height {
						width: 100%;
					}
				}
			}
		}
	}
}
/* ==========================================================================
   Product List shortcode responsive styles - end
   ========================================================================== */