/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - begin
   ========================================================================== */

@include laptop-landscape-mac{
	.mkdf-woo-single-page {
		
		.mkdf-single-product-content {
			.images{
				width: 65%;
			}
			
			.mkdf-single-product-summary {
				width: 35%;
				
				.cart{
					.mkdf-quantity-buttons{
						margin-right: 18px;
					}
				}
			}
		}
	}
}

@media only screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape) {
    
    .mkdf-woo-single-page {
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        padding: 12px 7px;
                    }
                }
            }
        }
    }
}

@include ipad-landscape {
    
    .mkdf-woo-single-page {
        
        .mkdf-single-product-content {
            
            .images,
            .mkdf-single-product-summary {
                float: none;
                width: 100%;
                padding: 0;
            }
	        
	        .images {
		
		        .woocommerce-product-gallery__trigger {
			        right: 10px;
		        }
	        }
            
            .mkdf-single-product-summary {
                margin: 30px 0 0;
            }
        }
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        padding: 13px 10px;
                    }
                }
            }
        }
    }
}

@include phone-landscape {
    
    .mkdf-woo-single-page {
	
	    .related.products,
	    .upsells.products {
		
		    > h2 {
			    line-height: 1.2em;
		    }
	    }
	    
	    .single_add_to_cart_button{
		    margin-top: 20px!important;
	    }
	
	    .woocommerce-tabs {
		
		    ul.tabs {
			
			    > li {
					margin-bottom: 20px;
			    }
		    }
		    #reviews {
			    h2 {
				    line-height: 1.2em;
			    }
		    }
	    }
	    
	
	    &.mkdf-woo-single-thumb-on-left-side {
		
		    .mkdf-single-product-content {
			
			    .images {
				
				    &.woocommerce-product-gallery--columns-4 {
					
					    .woocommerce-product-gallery__image {
						
						    &:not(:first-child) {
							    width: 50%;
						    }
					    }
				    }
			    }
		    }
	    }
        
        .mkdf-single-product-content {
            
            .images {
    
                &.woocommerce-product-gallery--columns-4 {
        
                    .woocommerce-product-gallery__image {
            
                        &:not(:first-child) {
	                        width: 50%;
                        }
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - end
   ========================================================================== */